* {
  transition: $effect-duration;
  padding: 0;
  margin: 0;
}

.page {
  scroll-behavior: smooth;
  font-family: 'Poppins', sans-serif;

  &__menu {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    transform: translateX(100%);

    &:target {
      transform: translateX(0);
    }
  }

  // forbid scrolling in menu
  &:has(.page__menu:target) {
    overflow: hidden;
  }

  &__body {
    background: $page-bg;
  }

  &__header {
    display: flex;
  }
}
